<template>
<v-container grid-list-xl fluid>
    <v-layout row wrap>

        <v-flex sm12>
            <h3>Usuário</h3>
        </v-flex>

        <v-flex xs12 sm12 md12>
            <v-container grid-list-xs>
                <v-layout row wrap>
                    <v-flex sm1 mt-3>
                        <div>
                            <v-avatar>
                                <v-icon :size="80">account_circle</v-icon>
                            </v-avatar>
                        </div>
                    </v-flex>
                    <v-flex sm11>
                        <div>
                            <div class="headline">{{client.name}}</div>
                            <span>{{client.email}}</span>
                            <br>
                            <v-chip color="primary" text-color="white">
                                {{client.funcao}}
                            </v-chip>
                        </div>
                    </v-flex>
                </v-layout>
            </v-container>
        </v-flex>
        <v-flex sm12>
            <v-expansion-panel>
                <v-expansion-panel-content v-for="(item,i) in painel" :key="i">
                    <div slot="header">{{item.nome}}</div>
                    <v-card>
                        <v-list two-line class="grey lighten-3">
                            <v-list-tile v-for="dado in item.dados">
                                <v-list-tile-content>
                                    <v-list-tile-title>{{dado.value}}</v-list-tile-title>
                                    <v-list-tile-sub-title>{{ dado.info }}</v-list-tile-sub-title>
                                </v-list-tile-content>
                            </v-list-tile>
                        </v-list>
                    </v-card>
                </v-expansion-panel-content>
            </v-expansion-panel>
        </v-flex>
        <v-flex sm12>
            <h3>Documentos</h3>
        </v-flex>

        <v-flex xs12 sm12 md12>
            <v-card>
                <v-card-title>
                    Arquivos
                    <v-spacer></v-spacer>
                </v-card-title>
                <v-data-table dark :headers="headers" :items="filers" class="elevation-1">
                    <template slot="items" slot-scope="props">
                        <td>{{ props.item.tipo }}</td>
                        <td>{{ props.item.title }}</td>
                        <td>{{ props.item.complemento }}</td>
                        <td>{{ props.item.createDate }}</td>
                        <td>
                            <v-btn flat icon @click="getDownalod(props.item._id)">
                                <v-icon>attach_file</v-icon>
                            </v-btn>
                        </td>
                    </template>
                </v-data-table>
            </v-card>
        </v-flex>
    </v-layout>
</v-container>
</template>

<script>
import services from "../services/userService";

export default {
    data() {
        return {
            user: {},
            client: {},
            search: '',
            filers: [],
            headers: [{
                    text: 'Tipo',
                    align: 'left',
                    sortable: false,
                    value: 'tipo'
                },
                {
                    text: 'Titulo',
                    value: 'titulo'
                },
                {
                    text: 'Complemento',
                    value: 'complemento'
                },
                {
                    text: 'Data de publicação',
                    value: 'data'
                },
            ],
            painel: [],
            id: ''
        }
    },
    methods: {
        listarArquivos(id) {
            services.getFiles(id).then(r => {
                if (r.status == 200) {
                    var arquivos = [];
                    var arquivo = {};
                    for (let index = 0; index < r.data.length; index++) {
                        arquivo = r.data[index];
                        arquivo.createDate = this.formatDate(r.data[index].createDate);
                        arquivos.push(arquivo);
                    }
                    this.filers = arquivos;
                }
            });
        },
        getDownalod(data) {
             var url = 'http://191.252.200.167:3000/file/download/' + data;
          //  var url = 'http://localhost:3000/file/download/' + data;

            window.open(url)
        },
        getUser(id) {
            services.getUser(id).then(r => {
                if (r.status == 200) {
                    this.client = r.data;
                    this.painel.push({
                        nome: 'Dados do trabalhador',
                        dados: [{
                                info: 'Nome do trabalhador',
                                value: this.client.name
                            },
                            {
                                info: 'CPF',
                                value: this.client.cpf
                            },
                            {
                                info: 'Número do pis',
                                value: this.client.numero_pis
                            },
                            {
                                info: 'Nacionalidade',
                                value: this.client.nacionalidade
                            },
                            {
                                info: 'Nome do pai',
                                value: this.client.pai
                            }, {
                                info: 'Nome da mãe',
                                value: this.client.mae
                            },
                            {
                                info: 'Local de nascimento',
                                value: this.client.local_nascimento
                            },
                            {
                                info: 'Estado Civil',
                                value: this.estadoCivil(this.client.estado_civil)
                            },
                            {
                                info: 'Sexo',
                                value: this.client.sexo
                            },
                            {
                                info: 'Função',
                                value: this.client.funcao
                            },
                            {
                                info: 'Salário',
                                value: this.client.salario
                            },
                            {
                                info: 'Variáveis do salario',
                                value: this.client.variaveis_salario
                            }
                        ]
                    }, {
                        nome: 'Dados complementares do trabalhador',
                        dados: [{
                                info: 'Reside no exterior',
                                value: this.resideNoExterior(this.client.complemento_trabalhador.residente_exterior)
                            },
                            {
                                info: 'Endereço',
                                value: this.client.complemento_trabalhador.endereco
                            },
                            {
                                info: 'Número',
                                value: this.client.complemento_trabalhador.numero
                            },
                            {
                                info: 'Bairro',
                                value: this.client.complemento_trabalhador.bairro
                            },
                            {
                                info: 'Complemento',
                                value: this.client.complemento_trabalhador.complemento
                            },
                            {
                                info: 'Cidade',
                                value: this.client.complemento_trabalhador.cidade
                            },
                            {
                                info: 'Cep',
                                value: this.client.complemento_trabalhador.cep
                            },
                            {
                                info: 'País',
                                value: this.client.complemento_trabalhador.pais
                            },
                            {
                                info: 'Residência própria',
                                value: this.resideNoExterior(this.client.complemento_trabalhador.residencia_propria)
                            }
                        ]
                    }, {
                        nome: 'Documentos de identificação',
                        dados: [{
                                info: 'Número da CTPS',
                                value: this.client.identificacao.num_ctps
                            },
                            {
                                info: 'Série da CTPS',
                                value: this.client.identificacao.serie_ctps
                            },
                            {
                                info: 'Unidade de federação da expedição da CTPS',
                                value: this.client.identificacao.unidade_ctps
                            },
                            {
                                info: 'RG',
                                value: this.client.identificacao.rg
                            },
                            {
                                info: 'Data de expedição',
                                value: this.formatar(this.client.identificacao.data_expedicao)
                            },
                            {
                                info: 'Orgão emissor',
                                value: this.client.identificacao.orgao_emissor
                            },
                            {
                                info: 'Data de validade do documento',
                                value: this.formatar(this.client.identificacao.data_validade)
                            }
                        ]
                    }, {
                        nome: 'Informações de contato',
                        dados: [{
                                info: 'Número de telefone fixo',
                                value: this.client.contato.telefone_fixo
                            },
                            {
                                info: 'Número de celular',
                                value: this.client.contato.celular
                            },
                            {
                                info: 'Endereço de email institucional',
                                value: this.client.contato.email_institucional
                            },
                            {
                                info: 'Endereço de email pessoal',
                                value: this.client.email
                            }
                        ]
                    }, {
                        nome: 'Escolaridade',
                        dados: [{
                            info: 'Informações',
                            value: this.escolaridade(this.client.escolaridade)
                        }]
                    }, {
                        nome: 'Dependentes',
                        dados: [{
                            info: '',
                            value: ''
                        }]
                    });
                }
            });
        },
        formatDate(date) {
            var mDate = new Date().toISOString().
            replace(/T/, ' '). // replace T with a space
            replace(/\..+/, '');
            if (!mDate) return null
            const [year, month, day] = mDate.split('-')
            const [mDay, horas] = day.split(' ')
            return `${mDay}/${month}/${year} - ${horas}`
        },
        formatar(date) {
            date
            if (!date) return null
            const [year, month, day] = date.split('-')
            const [mDay, horas] = day.split('T')
            return `${mDay}/${month}/${year}`
        },
        estadoCivil(num) {
            switch (num) {
                case 1:
                    return 'Solteiro'
                    break
                case 2:
                    return 'Casado'
                    break
                case 3:
                    return 'Divorciado'
                    break
                case 4:
                    return 'Viúvo'
                    break
                case 5:
                    return 'União estavel'
                    break
                default:
                    return 'Outro'
                    break
            }
        },
        resideNoExterior(num) {
            if (num == 1) {
                return 'Sim'
            }
            return 'Não'
        },
        escolaridade(id) {
            switch (id) {
                case 1:
                    return 'ANALFABETO'
                case 2:
                    return 'ATÉ A 4ª SÉRIE INCOMPLETA DO ENSINO FUNDAMENTAL (ANTIGO 1º GRAU OU PRIMÁRIO), OU QUE TENHA SE ALFABETIZADO SEM TER FREQUENTADO ESCOLA REGULAR'
                case 3:
                    return '4ª SÉRIE COMPLETA DO ENSINO FUNDAMENTAL (ANTIGO 1º GRAU OU GINÁSIO)'
                case 4:
                    return 'DA 5ª A 8ª SERIE DO ENSINO FUNDAMENTAL (ANTIGO 1º GRAU OU GINÁSIO)'
                case 5:
                    return 'ENSINO FUNDAMENTAL COMPLETO (ANTIGO 1º GRAU, PRIMÁRIO OU GINÁSIO)'
                case 6:
                    return 'ENSINO MÉDIO INCOMPLETO (ANTIGO 2º GRAU, SECUNDÁRIO OU COLEGIAL)'
                case 7:
                    return 'EDUCAÇÃO SUPERIOR INCOMPLETA'
                case 8:
                    return 'EDUCAÇÃO SUPERIOR INCOMPLETA'
                case 9:
                    return 'EDUCAÇÃO SUPERIOR COMPLETA'
                case 10:
                    return 'PÓS-GRADUAÇÃO'
                case 11:
                    return 'MESTRADO'
                case 12:
                    return 'DOUTORADO'
            }
        }
    },
    mounted() {
        this.user = this.user = JSON.parse(this.$cookie.get("user"));
        this.id = this.$route.params.id;
        this.listarArquivos(this.id);
        this.getUser(this.id);
    },
    computed: {
        filteredList() {
            return this.filers.filter(post => {
                return post.name.toLowerCase().includes(this.search.toLowerCase())
            })
        }
    }

}
</script>
